re. Los hay sin pestaña (solo la caja o abertura), con una pestaña o dos pestañas, algunos se confeccionan con tapa la cual puede ser externa, o estar pegada al interior del bolsillo y salir por medio de la abertura o ribete, también los hay con cremallera.")]),_c('div',{staticClass:"row justify-content-center align-items-stretch mt-5"},[_c('div',{staticClass:"col-lg-5"},[_c('div',{staticClass:"BorTit"},[_c('b',[_vm._v("Figura 6")]),_vm._v(" Bolsillo de ribete posterior")]),_c('img',{staticClass:"img-t img-a my-3",attrs:{"data-aos":"zoom-in","src":require("@/assets/ilustraciones/img13.png"),"alt":""}})]),_c('div',{staticClass:"col-lg-7"},[_c('div',{staticClass:"BorTit"},[_c('b',[_vm._v("Figura 7")]),_vm._v(" Bolsillo ribete")]),_c('img',{staticClass:"img-t img-a my-3",attrs:{"data-aos":"zoom-in","src":require("@/assets/ilustraciones/img14.png"),"alt":""}}),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-12 cajon color-primario px-1"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-8"},[_c('p',{staticClass:"my-3"},[_vm._v("Para realizar este tipo de operación se hace necesario cerrar las costuras laterales respetando las aberturas para los bolsillos. Separar los márgenes de costura con la plancha. Observemos los siguientes videos para ampliar la información. ")])])])])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-4 border-row text-center my-3",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"card-row p-4 boton-row"},[_c('h4',{staticClass:"text-white"},[_vm._v("Video 1 ")]),_c('p',{staticClass:"text-white"},[_vm._v("How to sew a pocket.2 sewing course ")])]),_c('a',{staticClass:"boton--sm color-acento-contenido text-white",attrs:{"href":"https://www.youtube.com/watch?app=desktop&v=on2kcoGPR3Q&feature=youtu.be","target":"_blank","type":"application/pdf"}},[_c('span',{staticClas